.sb-cls-ctn {
    cursor: pointer;
    height: 20px;
    position: absolute;
    right: 0;
    top: 0;
    width: 20px;
    z-index: 2
}

.sb-cls-ctn:after,
.sb-cls-ctn:before {
    background: #696d80;
    content: "";
    height: 2px;
    left: 1px;
    position: absolute;
    top: 6px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    width: 20px
}

.sb-cls-ctn:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg)
}

.sb-modal-cls {
    right: 16px;
    top: 16px
}

.sb-loadingbar-ctn {
    background: rgba(227, 79, 14, .25);
    bottom: 0;
    height: 5px;
    left: 0;
    position: absolute;
    width: 100%;
    z-index: 999999999999999
}

.sb-loadingbar-ctn:after,
.sb-loadingbar-ctn:before {
    background: #fe544f;
    content: "";
    height: 5px;
    position: absolute;
    top: 0;
    z-index: 9
}

.sb-loadingbar-ctn:before {
    -webkit-animation: sb-loading-animation 4s infinite;
    animation: sb-loading-animation 4s infinite
}

.sb-loadingbar-ctn:after {
    -webkit-animation: sb-loading-animation 4s 2s infinite;
    animation: sb-loading-animation 4s 2s infinite
}

@-webkit-keyframes sb-loading-animation {
    0% {
        left: -5%;
        width: 0
    }

    to {
        left: 130%;
        width: 50%
    }
}

@keyframes sb-loading-animation {
    0% {
        left: -5%;
        width: 0
    }

    to {
        left: 130%;
        width: 50%
    }
}

