.sb-fs-loader-border {
    border: 6px solid green;
    border-radius: 50%;
    box-sizing: border-box
}

.sb-fs-loader-border,
.sb-fs-loader-img {
    height: inherit;
    left: 0;
    position: absolute;
    top: 0;
    width: inherit
