.sb-modal-message{
    row-gap: 20px;
    text-align: center;
    padding: 40px 15%;
    row-gap: 20px;
}
.sb-modal-msg-content{
    row-gap: 10px;
}
.sb-modal-msg-btns{
    column-gap: 15px;
}
.sb-modal-m-icon{
    height: 68px;
    width: 68px;
    border-radius: 50px;
}
.sb-modal-m-icon[data-type="style1"] {
    color: #0068A0;
    background-color: #F3F4F5;
}
.sb-modal-m-icon[data-type="style2"] {
    color: #59AB46;
    background-color: #E6F4E3;
}

.sb-modal-m-icon[data-type="style3"] {
    background-color: #fff;
    border: 1px solid #E6E6EB;
}
.sb-modal-msg-icon[data-type="pr-icons"]{
    flex-direction: row;
    align-items: flex-end;
}
.sb-modal-msg-icon[data-type="pr-icons"] > div:not(:first-of-type){
    margin-left: -15px;
    z-index: 2;
}

.sb-modal-m-num{
    width: 26px;
    height: 26px;
    color: #fff;
    border-radius: 50px;
    background-color: #696D80;
    font-weight: 800;
    font-size: 13px;
}
.sb-modal-message[data-style="fixed-elements"]{
    padding: 0px;
}
.sb-modal-message[data-style="fixed-elements"] .sb-modal-msg-content{
    padding: 0px 15%;
}
.sb-modal-message[data-style="fixed-elements"] .sb-modal-msg-btns{
    border-top: 1px solid #eee;
    padding: 15px 25px;
    justify-content: flex-end;
    margin-top: 35px;
}
.sb-modal-message[data-style="fixed-elements"] .sb-modal-msg-heading {
    border-bottom: 1px solid #eee;
    padding: 15px 25px;
    justify-content: flex-start;
    margin-bottom: 35px;
    flex-direction: column;
    text-align: left;
}
.sb-modal-message[data-style="fixed-elements"][data-screen="addapikey"] .sb-modal-msg-heading {
    margin-bottom: 15px;
}
.sb-modal-msg-api-keys{
    flex-direction: column;
    padding: 0px 25px;
    row-gap: 20px;
    padding-bottom: 30px;
}

.sb-modal-provider-row{
    border: 1px solid #E6E6EB;
    border-radius: 5px;
    flex-direction: column;
}
.sb-modal-provider-error{
    padding-top: 15px;
    color: var(--red-dflt);
}
.sb-modal-provider-add-in,
.sb-modal-provider-link,
.sb-modal-provider-success{
    flex-direction: column;
    column-gap: 10px;
}
.sb-modal-provider-ch{
    align-items: flex-start;
    padding: 10px 20px;
    column-gap: 10px;
}
.sb-modal-provider-add-in{
    padding: 0 20px;
    margin-top: 10px;
}

.sb-modal-provider-link{
    border-top: 1px solid #E6E6EB;
    color: #0068A0;
    background: #F9F9FA;
    font-size: 13px;
    font-weight: 600;
    padding: 15px 0px;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    margin-top: 20px;
}
.sb-modal-provider-link a,
.sb-modal-provider-link a:focus{
    text-decoration: none;
    outline: none;
}
.sb-modal-provider-success{
    padding: 50px 40px;
    font-size: 18px;
    background: #F9F9FA;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
.sb-modal-success-icon{
    width: 25px;
    height: 25px;
    color: #fff;
    background: #59AB46;
    border-radius: 50px;
}
.sb-modal-provider-input{
    column-gap: 10px;
}
.sb-support-error-top{
    display: inline-flex;
    column-gap: 10px;
    margin-left: auto;
}

.sb-support-errors-list .sb-feedslist-table{
    border: 1px solid #e1e1e1;
    margin: 25px 0px;
    box-shadow: unset;
    font-size: 12px;
}
.sb-support-errors-list .sb-feedslist-table .sb-feedslist-thtf th{
    font-weight: 600;
}
.sb-error-code{
     display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    padding: 3px 4px;
    border-radius: 15px;
    font-size: 10px;
    color: #FF9800;
    background-color: #ffeb3b3d;
}
.sb-error-code[data-code="404"]{
    color: #d72c2c;
    background-color: #d72c2c2e;
}
.sb-error-code[data-code="403"] {
